/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Hybrid Fryer","date":"27 May 2009","slug":"/2009-05-27-hybrid-fryer/","tags":"Y2009"},"html":"<p>Just to show that we not only make <a href=\"http://www.papascott.de/archives/2009/04/15/stripes-from-paris\">sexy investments with French models</a> (not to be confused with promotions with <a href=\"http://www.abendzeitung.de/leute/108462\">German top models</a>), this morning at our restaurant in Dibbersen we installed the new <a href=\"http://www.papascott.de/archives/2009/04/15/stripes-from-paris\">low-oil-volume (LOV) fryer</a> that we've been planning for over a year, replacing a double vat with a new triple vat.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3570794066\" title=\"View 'Hybrid Fryer' on Flickr.com\"><img src=\"https://farm4.static.flickr.com/3641/3570794066_cebd1281e2.jpg\" alt=\"Hybrid Fryer\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>Astute readers will note that this is not the Frymaster model we were considering last year, but is instead manufactured by <a href=\"http://www.hennypenny.com/\">Henny Penny</a>. The principle is however the same. The low-oil-volume vat holds just half the volume of a traditional fryer, so we only have to heat half as much oil. To maintain the quality of the oil, the fryer automatically filters the vat after 12 cooking cycles (normally a vat is filtered once daily), and tops itself off when the vat starts to run low.</p>\n<p>Think of the fryer as just like a hybrid car... the tank is smaller and we have to fill it less often, so we save energy and money. We're looking for both good performance and good gas (er, I mean oil) mileage.</p>"},"next":{"fields":{"title":"Rustic furniture","date":"13 May 2009","slug":"/2009-05-13-rustic-furniture/"}},"previous":{"fields":{"title":"Spur of the Moment","date":"27 May 2009","slug":"/2009-05-27-spur-of-the-moment/"}}},{"node":{"fields":{"title":"Rustic furniture","date":"13 May 2009","slug":"/2009-05-13-rustic-furniture/","tags":"Y2009"},"html":"<p>Seeing <a href=\"http://www.martinisfortwo.com/2009/05/movin-on-up.html\">Diane's report</a> of moving her \"super-sized, American-made furniture\" from Mannheim to Barcelona reminded me that today was a minor expat milestone for us. Today we got rid of the last remaining bits of American furniture we had brought with us back in 1990. It was a bulky oak dining room set that Germans would politely describe as \"rustic\". We had tried selling it a few years ago, but found no takers. It's been in the basement since then, and as part of our long-term 2009 basement project it landed on the pile of \"Sperrm&uuml;ll\" that was to be picked up today.</p>\n<p>(As an aside, how would one refer to \"Sperrm&uuml;ll\" pickup in English? My father would call it the \"dray service\", but that's a quaint 19th century term that's a bit of an inside joke in our family. \"Junk pickup\" &agrave; la <a href=\"http://en.wikipedia.org/wiki/Sanford_and_Son\">Sanford &amp; Son</a> maybe?)</p>\n<p>However, the furniture will not end up at the dump. The table was picked up a couple weeks ago by a flea-market collector whom we had called to take a look at our stuff. And the 4 chairs disappeared from the pile on the street yesterday, one day before today's pickup. Sperrm&uuml;ll moves in mysterious ways.</p>"},"next":{"fields":{"title":"Politically Active","date":"11 May 2009","slug":"/2009-05-11-politically-active/"}},"previous":{"fields":{"title":"Hybrid Fryer","date":"27 May 2009","slug":"/2009-05-27-hybrid-fryer/"}}},{"node":{"fields":{"title":"Politically Active","date":"11 May 2009","slug":"/2009-05-11-politically-active/","tags":"Y2009"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3522786088\" title=\"View 'photo-6' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3364/3522786088_e4f4621b8f_m.jpg\" alt=\"photo-6\" border=\"0\" width=\"240\" height=\"180\" align=\"right\" /></a>In a moment of weakness after a week of working middle-shifts, Frauke agreed to take Christopher for an outing this past weekend... only after promising the trip did she see where he wanted to go: to a demonstration in Hannover against policy of the current education ministry. That's a cause we can agree with, but taking up an entire Saturday with a bus trip to Hannover? And, yes, Christopher insisted on taking the bus.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3517884980\" title=\"View 'photo-4' on Flickr.com\"><img src=\"http://static.flickr.com/3322/3517884980_ca57ec61bb_m.jpg\" alt=\"photo-4\" border=\"0\" width=\"\" height=\"\" align=\"left\" /></a>I was not in on the promise, having taken the time to read the fine print. I did however bring to two of them to the bus pick-up point at 9:30 Saturday morning. We managed to get Christopher to compromise on taking the bus there, but to take the train back.</p>\n<p>And they had a good crowd. 10000 demonstrators (mostly teachers and students) <a href=\"http://www.haz.de/Hannover/Aus-der-Stadt/Uebersicht/10.000-demonstrieren-gegen-Schulpolitik\">protested plans to impose a shortened Abitur in the \"Gesamtschule\"</a> (combined schools, like we hope to have built in Jesteburg) and the general lack of funding for education.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3517074111\" title=\"View 'photo-5' on Flickr.com\"><img src=\"http://static.flickr.com/3611/3517074111_b02fc60699_m.jpg\" alt=\"photo-5\" border=\"0\" width=\"\" height=\"\" align=\"right\" /></a>Frauke and Christopher skipped out after two hours, eating lunch at McDonald's amd taking the ICE back home. They were back by 4 pm, giving Christopher plenty of time to mow the lawn before dark and letting Frauke relax a little bit on her day off and enjoy an outdoor cook-out by yours truly. </p>\n<p>The following day was Mother's Day, and Frauke celebrated by doing something she loves... planting flowers, in this case at her restaurant at Hamburg Winsener Strasse. </p>"},"next":{"fields":{"title":"East German Nostalgia","date":"06 May 2009","slug":"/2009-05-06-east-german-nostalgia/"}},"previous":{"fields":{"title":"Rustic furniture","date":"13 May 2009","slug":"/2009-05-13-rustic-furniture/"}}}],"pathPrefix":"","first":false,"last":false,"index":58,"pageCount":1101}}

/***/ })

